
import PostAPI from "./postAPI.js" 
import{ createPostEl } from './Utils.js'
// get pots btn
const getpostsBtn = document.querySelector('.get-posts-btn')
getpostsBtn.addEventListener('click', async ()=>{
    try{
        // get posts 
        // show loading inside the resultEl
        const resultEl = document.querySelector ('#result')
        // insert "loading"
        resultEl.innerHTML = 
        '<sl-spinner style ="font-size:3em; --stroke-width: 6px"> </sl-spinner>'


        //  GET POSTS 
        const posts = await PostAPI.getPosts()
        console.log(posts)
        // remove the loading text from result element

        resultEl.innerHTML = ''
        // loop through each post in posts 
        posts.forEach(post =>{
        // create postEl(div)

        const postEl=createPostEl(post)
        // append (insert) into the #result div 
        // get results resultEl

        resultEl.append(postEl)
        // animate postEls
        gsap.fromTo('.anim-in', {opacity:0, y:-5}, {opacity:1, y: 0, ease:"power2.out", duration:0.6, stagger:0.5})


        })

    }catch(err){
        alert(err)

    }
})



